import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { OrderTrackingComponent } from './order-tracking.component'
import { OtMainComponent } from './ot-main/ot-main.component';
import { OtSearchComponent } from './ot-search/ot-search.component';
import { OtDetailComponent } from './ot-detail/ot-detail.component';


const orderTrackingRoutes: Routes = [
  {
    path: '', component: OrderTrackingComponent,
    children: [
      {
        path: '', component: OtMainComponent,
        data: {
              topbar: {
                urlBackLink: '/index',
                internal: false,
                headerText: 'รายการสั่งซื้อ',
                backlink : true,
                alert: {
                  show: false,
                }
              },
            }
      },
      {
        path: 'search', component: OtSearchComponent,
        data: {
              topbar: {
                urlBackLink: '/order-tracking',
                internal: false,
                headerText: 'SEARCH',
                backlink : true,
                alert: {
                  show: false,
                }
              }
        }
      },
      {
        path: ':loadTest', component: OtMainComponent,
        data: {
          topbar: {
            urlBackLink: '/index',
            internal: false,
            headerText: 'รายการสั่งซื้อ',
            backlink : true,
            alert: {
              show: false,
            }
          }
        }
      },
      {
        path: 'detail', component: OtDetailComponent,
        data: {
              topbar: {
                urlBackLink: '',
                internal: false,
                headerText: 'รายละเอียดคำสั่งซื้อ',
                backlink : true,
                alert: {
                  show: false,
                }
              }
        }
      },
      {
        path: 'detail/:sapNo',
        component : OtDetailComponent,
        data: {
          topbar: {
            urlBackLink: '',
            internal: false,
            headerText: 'รายละเอียดคำสั่งซื้อ',
            backlink : true,
            alert: {
              show: false,
            }
          }
        }
      },
      {
        path: 'detail/:sapNo/:loadTest',
        component : OtDetailComponent,
        data: {
          topbar: {
            urlBackLink: '',
            internal: false,
            headerText: 'รายละเอียดคำสั่งซื้อ',
            backlink : true,
            alert: {
              show: false,
            }
          }
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(orderTrackingRoutes)],
  exports: [RouterModule]
})

export class OrderTrackingRoutingModule {
}



