import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { HttpModule, JsonpModule } from '@angular/http';
import { OrderTrackingRoutingModule } from './order-tracking-routing.module';
import { OrderTrackingComponent } from './order-tracking.component'
import { RouterModule } from '@angular/router';

import { ServiceShared } from '../shared/service.module'
import { OtSearchService } from './ot-search/ot-search.service'
import { TopbarSharedModule } from '../shared/topbar.module';
import { OtMainComponent } from './ot-main/ot-main.component';
import { OtSearchComponent } from './ot-search/ot-search.component';
import { OtDetailComponent } from './ot-detail/ot-detail.component';
import { OtDetailCardService } from './shared/ot-detail-card/ot-detail-card.service'
import { OtDetailCardModel}  from './shared/ot-detail-card/ot-detail.card.model'
import { OtCardComponent } from './shared/ot-card/ot-card.component';
import { otCardService } from './shared/ot-card/ot-card.service';
import { OtDetailCardComponent } from './shared/ot-detail-card/ot-detail-card.component';
import { DataForm } from '../services/form'
import { CommonFunc } from '../shared/config';

@NgModule({
  imports: [
    CommonModule,
    OrderTrackingRoutingModule,
    RouterModule,
    TopbarSharedModule,
    FormsModule,
    ServiceShared
  ],
  declarations: [
    OrderTrackingComponent, 
    OtMainComponent, 
    OtSearchComponent, 
    OtDetailComponent, 
    OtCardComponent, 
    OtDetailCardComponent,
  ],
  exports: [
      OrderTrackingComponent
  ],
  providers: [ 
    otCardService, 
    DataForm, 
    OtDetailCardService, 
    OtDetailCardModel, 
    CommonFunc, 
    OtSearchService 
  ]
})
export class OrderTrackingModule { }



