import { Component, OnInit , Input} from '@angular/core';
import { OtSearchService } from "./ot-search.service";
import {DataForm} from '../../services/form'
import { ActivatedRoute, Router } from '@angular/router';
import {CommonFunc} from '../../shared/config'
import { UserInfo } from './../../services/userInfo'

declare let $: any;

@Component({
  selector: 'app-ot-search',
  templateUrl: './ot-search.component.html',
  styleUrls: ['./ot-search.component.scss']
})
export class OtSearchComponent implements OnInit {

  private data: any = this.otSearchService.data;
  calendar: Date;
  public responseTxt : string = '';
  name: string = '';
  SAPNo: string = this.otSearchService.SAPNo;
  PONo: string = this.otSearchService.PONo;
  startDate: string= this.otSearchService.startDate;
  endDate: string = this.otSearchService.endDate;
  minStart : string = this.otSearchService.minStart;
  minEnd : string = this.otSearchService.minEnd;
  maxStart: string = this.otSearchService.maxStart;
  maxEnd: string = this.otSearchService.maxEnd;

  constructor(
      public otSearchService : OtSearchService,
      private dataForm : DataForm,
      private router : Router,
      private func : CommonFunc,
      private user : UserInfo
  ) {
  }

   public getFullValue(val : number) {
      if(val.toString().length === 1) {
          return '0'+val;
      }
      return val
    }

    public changeDate(eleRef : string) {
        if(eleRef === "end") {
            if(this.endDate !== "") {
                let date = new Date(this.endDate);
                date.setDate(date.getDate() - 7);
                this.minStart = date.getFullYear() +
                    "/" +
                    this.getFullValue(date.getMonth() + 1) +
                    "/" +
                    this.getFullValue(date.getDate());
                this.maxStart = this.endDate;
            } else {
                this.minStart = "2014-01-01";
                this.maxStart = new Date().toJSON().split('T')[0];
            }
        } else {

            if(this.startDate !== "") {
                let date = new Date(this.startDate);
                date.setDate(date.getDate() + 7);
                this.maxEnd = date.getFullYear() +
                    "/" +
                    this.getFullValue(date.getMonth() + 1) +
                    "/" +
                    this.getFullValue(date.getDate());
                this.minEnd = this.startDate;
            } else {
                this.minEnd = "2014-01-01";
                this.maxEnd = new Date().toJSON().split('T')[0];
            }
        }
    }

    ngOnInit() {

    }

    routeToDetail(sapId : string, orderId : string){
        this.router.navigate(['/order-tracking/detail/'+sapId+"|"+orderId]);
    }

  private onSubmit() {
      let that = this
      let params: any = {
          SAPNo : this.SAPNo,
          PONo : this.PONo,
          startDate : this.startDate,
          endDate : this.endDate,
          DealerCode: this.user.getDealerCode()
      }

      if(
          (this.SAPNo !== undefined || this.PONo !== undefined) ||
          (this.startDate !== undefined && this.endDate !== undefined)) {
          this.data = [];
          $("#preload").css("display", "block");
          this.dataForm.getOrderTracking(params)
              .then( res  => {
                  $("#preload").css("display", "none");
                  if(typeof res.error !== 'undefined') {
                      this.responseTxt = res.error;
                      this.data = [];
                      this.otSearchService.setData(this);
                  } else if(typeof res.listMonitorOrderMst !== 'undefined' &&  res.listMonitorOrderMst.length > 0) {
                      this.responseTxt = '';
                      this.data = [];
                      this.dataForm.orderTracking = [];

                      for (let i in res.listMonitorOrderMst) {
                          let item = res.listMonitorOrderMst[i]
                          if(typeof item.sapSoAmt === 'undefined') {
                              item.sapSoAmt = 0;
                          }

                          let dt = {
                              reqOrderNo: item.reqOrderNo,
                              sapSoAmt: that.func.formatNumber(item.sapSoAmt),
                              reqOrderDt: item.reqOrderDt,
                              sapDescription: item.sapDescription,
                              reqDealerName: item.reqDealerName,
                              shipTo: item.shipTo,
                              sap : item.sapOrderShow,
                              sapOrderId : item.sapOrderShow,
                              poNoShow : item.poNoShow
                          }
                          this.dataForm.orderTracking.push(dt)
                          this.data.push(dt);
                          this.otSearchService.setData(this);
                      }
                  } else if(typeof res.resultCode !== 'undefined' && res.resultCode === 'F') {
                      this.responseTxt = res.resultMessage
                      this.data = [];
                      this.otSearchService.setData(this);
                  } else {
                      this.responseTxt = 'ไม่พบรายการสั่งซื้อ';
                      this.data = [];
                      this.otSearchService.setData(this);
                  }
          });
      }
  }

}



