import { environment } from './../../../../environments/environment.pvt';
import { Injectable } from '@angular/core';
import {OtDetailCardModel} from './ot-detail.card.model'
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';
import { Http, Response,RequestOptions,Headers ,URLSearchParams } from '@angular/http';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';
import { UserInfo } from './../../../services/userInfo'
import { DataForm } from './../../../services/form';
import { CommonFunc } from './../../../shared/config'
import { AuthHttpService } from 'client-mypartner-lib/src/services/auth/auth-http.service';

@Injectable()
export class OtDetailCardService {
  public url= '/api/mypartner/';
  public subject: any = new Subject<any>();
  public obj: any;


  constructor(
      private otDetailCardModel : OtDetailCardModel,
      private http : Http,
      private formSV : DataForm,
      private common : CommonFunc,
      private authHttpService : AuthHttpService,
      private service : UserInfo
  ) {
    this.obj = this.otDetailCardModel;
  }

  public setContent(data: any, SAPNo: string) {
      let SAPNoConverted = SAPNo.split('|');
      this.obj = new OtDetailCardModel();
      let DT = data.orderDetail;
      let refData : any;
      let that = this

      this.formSV.orderTracking.map(function(obj, i){
          if(obj !== undefined && obj.sap == SAPNoConverted[0]) {
              refData = obj;
          }
      });

      if(refData !== undefined) {
          this.obj.PONo = refData.poNoShow
          this.obj.ORNo = refData.sapOrderId
          this.obj.PODate = refData.reqOrderDt
          this.obj.POStatus = refData.sapDescription
          this.obj.Total = refData.sapSoAmt
      }
      let AmountNoVat : number =  0;
      let AmountBFVat : number = 0;

      DT.items.map(function(obj, i) {
          if(obj.vat !== null && obj.vat !== 0 && obj.vat !== '' && obj.vat !== undefined) {
              AmountBFVat += obj.totalAmount
          } else {
              AmountNoVat += obj.totalAmount
          }
          that.obj.ORList.push({
            name: obj.matDesc + ' ' + obj.qty + ' ชิ้น',
            val: that.common.formatNumber(obj.netAmount)
          });
      })

      this.obj.AmountNoVat = that.common.formatNumber(AmountNoVat);
      this.obj.AmountBFVat = that.common.formatNumber(AmountBFVat);
      this.obj.Vat = that.common.formatNumber(DT.sumVat)
      this.obj.Discount = that.common.formatNumber(Math.abs(DT.sumDiscount));
      this.obj.Withholding = that.common.formatNumber(DT.sumWithHoldingTax);
      this.obj.PayBy = DT.changePayType
      this.obj.NameOnTax = DT.reqTaxName
      this.obj.AddOnTax = DT.reqTaxAddr
      this.obj.NameOnShip = DT.reqShipToName
      this.obj.AddOnShip = DT.reqShipToAddr



      this.subject.next({
          obj : this.obj
      })
  }

    getOrderTrackingDetailTest(agv : string){
        let agr = agv.split('|');
        let data = {'reqOrderNo': agr[1],'sapOrderNo': agr[0]};
        return this.authHttpService.post('test/orderTrackingDetail',
            data).toPromise()
            .then(this.extractData)
            .catch(this.handleError);
    }

    getOrderTrackingDetail(agv : string){
        let agr = agv.split('|');
        let data = {
            'reqOrderNo': agr[1],
            'sapOrderNo': agr[0],
            'temp' : this.service.gToken(),
            'temp_' : this.service.getAccessToken()
        };
        return this.authHttpService.post('orderTrackingDetail',
            data).toPromise()
            .then(this.extractData)
            .catch((error : Response | any) => {
                $("#preload").css("display", "none");
                let errMsg: string;
                if (error instanceof Response){
                    const body = error.json() || '';
                    const err = body.error || JSON.stringify(body);
                    errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
                }else{
                    errMsg = error.message ? error.message : error.toString();
                }
                return Observable.throw(errMsg);
            });
    }

    private getPutHeaders(){
        let headers = new Headers();
        headers.append('Content-Type', 'application/json');
        return  new RequestOptions({
            headers: headers
        });
    }
    private extractData(res){
        return res;
    }

    private handleError(error: Response | any){
        let errMsg: string;
        if (error instanceof Response){
            const body = error.json() || '';
            const err = body.error || JSON.stringify(body);
            errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        }else{
            errMsg = error.message ? error.message : error.toString();
        }
        return Observable.throw(errMsg);
    }

  getObserver (): Observable<any> {
    return this.subject.asObservable();
  }
}



