import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';
import { Http, Response,RequestOptions,Headers ,URLSearchParams } from '@angular/http';

@Injectable()
export class OtSearchService {
    public data: any = [];
    public SAPNo: string;
    public PONo: string;
    public startDate: string;
    public endDate: string;
    public status: string;
    public minStart: string  = "2014-01-01";
    public maxStart : string = new Date().toJSON().split('T')[0];
    public minEnd: string = "2014-01-01";
    public maxEnd: string = new Date().toJSON().split('T')[0];
    public subject: any = new Subject<any>();

  constructor(private http : Http) {

  }

    public setData (agr: any) {
        this.data = agr.data
        this.SAPNo = agr.SAPNo
        this.PONo = agr.PONo
        this.startDate = agr.startDate
        this.endDate = agr.endDate
        this.status = agr.status
        this.minStart = agr.minStart
        this.maxStart = agr.maxStart
        this.minEnd = agr.minEnd;
        this.maxEnd = agr.maxEnd;
  }
}



