import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataForm } from '../../services/form'
import { CommonFunc } from '../../shared/config'
import { UserInfo } from './../../services/userInfo'
declare let $: any;

@Component({
  selector: 'app-ot-main',
  templateUrl: './ot-main.component.html',
  styleUrls: ['./ot-main.component.scss']
})

export class OtMainComponent implements OnInit {
  private data: any = [];
  public responseTxt : string = '';
  private loadTest : boolean = false;

  constructor(
    private route : ActivatedRoute,
    private router:Router,
    private dataForm:DataForm,
    private func : CommonFunc,
    private user : UserInfo
  ) {
    route.params.subscribe((params) => {
      let param = params['loadTest']
      if(param) {
        this.loadTest = true;
      }
    })
  }

  ngOnInit() {
    if(this.loadTest) {
      this.dataForm.getOrderTrackingTest().then(res => {
      });
    } else {

      let that = this
      let date = new Date();
      let end = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate()
      date.setDate(date.getDate() - 3);
      let start = date.getFullYear() + "/" + (date.getMonth() + 1) + "/" + date.getDate()

      let params: any = {
        SAPNo: '',
        PONo: '',
        startDate: start,
        endDate: end,
        DealerCode: this.user.getDealerCode()
      }

      $("#preload").css("display", "block");
      this.dataForm.getOrderTracking(params).then(res => {
        $("#preload").css("display", "none");
        if (typeof res.error !== 'undefined') {
          this.responseTxt = res.error;
        } else if (typeof res.listMonitorOrderMst !== 'undefined' && res.listMonitorOrderMst.length > 0) {
          this.data = [];
          this.dataForm.orderTracking = [];

          for (let i in res.listMonitorOrderMst) {
            let item = res.listMonitorOrderMst[i]
            if (typeof item.sapSoAmt === 'undefined') {
              item.sapSoAmt = 0;
            }
            let dt = {
              reqOrderNo: item.reqOrderNo,
              sapSoAmt: that.func.formatNumber(item.sapSoAmt),
              reqOrderDt: item.reqOrderDt,
              sapDescription: item.sapDescription,
              reqDealerName: item.reqDealerName,
              shipTo: item.shipTo,
              sap: item.sapOrderShow,
              sapOrderId: item.sapOrderShow,
              poNoShow : item.poNoShow
            }

            this.dataForm.orderTracking.push(dt)
            this.data.push(dt);
          }
        } else {
          this.responseTxt = 'ไม่พบรายการสั่งซื้อ';
        }
      });
    }

  }

  linkToSearch(){
    this.router.navigate(['/order-tracking/search']);
  }

  linkToSort() {
    let item = [];
    for(let i = (this.data.length -1); i >= 0 ; i--) {
      item.push(this.data[i]);
    }
    this.data = item;

  }
  routeToDetail(sapId : string, orderId : string){
    this.router.navigate(['/order-tracking/detail/'+sapId+"|"+orderId]);
  }

}



