import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Router, NavigationEnd} from '@angular/router';
declare let $: any;

@Component({
  selector: 'app-order-tracking',
  templateUrl: './order-tracking.component.html',
  styleUrls: ['./order-tracking.component.scss']
})
export class OrderTrackingComponent implements OnInit {
  public headerTxt : string;
  public backLink : boolean = false;
  public urlBackLink : string;
  public alert : any = {};


  constructor(
      route: ActivatedRoute,
      router: Router
  ) {
    let that = this;

    router.events
        .filter((event) => event instanceof NavigationEnd)
        .map(() => route)
        .subscribe((event) => {
          event.firstChild.data.subscribe( data => {
            that.headerTxt = data.topbar.headerText;
            that.backLink = data.topbar.backlink
            that.urlBackLink = data.topbar.urlBackLink
            that.alert = data.topbar.alert
          });
        });
  }

  public onActivate(event : any) {
  }

  ngOnInit() {
    $('body').css('background', '#e8e5e5');
  }

}



