import { Component, OnInit } from '@angular/core';
import {OtDetailCardService} from './ot-detail-card.service'
import { Subscription } from 'rxjs/Subscription';
import {OtDetailCardModel} from './ot-detail.card.model'
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ot-detail-card',
  templateUrl: './ot-detail-card.component.html',
  styleUrls: ['./ot-detail-card.component.scss']
})
export class OtDetailCardComponent implements OnInit {
    subscription: Subscription;
    sapNo: string;
    private loadTest : boolean = false
    private sub: any;
    private data : any = new OtDetailCardModel();

  constructor(
      private otDetailCardService : OtDetailCardService,
      private route: ActivatedRoute
  ) {
      route.params.subscribe((params) => {
          let param = params['loadTest']
          if(param) {
              this.loadTest = true;
          }
      })
      this.subscription = this.otDetailCardService
          .getObserver()
          .subscribe( (message: any) => {
        this.data = message.obj
      });
  }

  ngOnInit() {

      if(this.loadTest) {
          this.sub = this.route.params
              .subscribe(params => {
                  this.sapNo = params['sapNo'];
                  this.otDetailCardService
                      .getOrderTrackingDetailTest(this.sapNo)
                      .then(res => {
                          this.otDetailCardService
                              .setContent(res, this.sapNo);
                      });
              });

      } else {

          this.sub = this.route.params
              .subscribe(params => {
                  this.sapNo = params['sapNo'];
                  $("#preload").css("display", "block");
                  this.otDetailCardService
                      .getOrderTrackingDetail(this.sapNo)
                      .then((res) => {
                          $("#preload").css("display", "none");
                          this.otDetailCardService
                              .setContent(res, this.sapNo);
                      });
              });
      }
  }

}



