import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { otCardService } from "./ot-card.service";

@Component({
  selector: 'app-ot-card',
  templateUrl: './ot-card.component.html',
  styleUrls: ['./ot-card.component.scss']
})
export class OtCardComponent implements OnInit{
  @Input('trackNo') trackNo: string;
  @Input('total') total: string;
  @Input()
  set date(date : string) {
    let dateSp = date.split(' ')
    this.date_ = dateSp[0]
  }
  public date_ : string = '';
  @Input('time') time: string;
  @Input('status') status: string;

  subscription: Subscription;
  private iconPath:string
  private iconPathMap = [
    {"name": "จัดส่งสินค้า","path":"./assets/images/icon/delivered.png"},
    {"name": "Pending Verify","path":"./assets/images/icon/packing.png"},
    {"name": "Wait Delivery", "path": "./assets/images/icon/deliver.png"},
    {"name" : "Over Due" , "path" : "./assets/images/icon/overdue.png"},
    {"name" : "Cancelled" , "path" : "./assets/images/icon/cancel.png"},
    {"name" : "Goods Receive" , "path" : "./assets/images/icon/goodrecieve.png"},
    {"name" : "Complete" , "path" : "./assets/images/icon/complete.png"},
    {"name" : "Delivered" , "path" : "./assets/images/icon/delivered.png"},
  ]
  private iconDefaultPath = "./assets/images/icon/default.png"

  constructor(private OtCardService: otCardService) {
    this.subscription = this.OtCardService.getObserver().subscribe( (message: any) => {
      this.trackNo = message.trackNo;
      this.total = message.total;
      this.date = message.date;
      this.time = message.time;
      this.status = message.status;
    });
    this.iconPath = this.iconDefaultPath    
    this.iconPathMap.map((obj,index)=>{
      if(obj.name === this.status){
        this.iconPath = obj.path
      }
    })
  }

  ngOnInit() {
    this.iconPath = this.iconDefaultPath    
    this.iconPathMap.map((obj,index)=>{
      if(obj.name === this.status){
        this.iconPath = obj.path
      }
    })
  }

}



